<template><section>

<h1>动画组件</h1>
<p>内置的动画效果，方便使用</p>
<h2 id="collapsetransition"><a class="header-anchor" href="#collapsetransition" aria-hidden="true">¶</a> CollapseTransition</h2>
<p>折叠组件，可以参看<code class="code_inline">collapse</code>组件的动画效果</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-item2 demo-item3\&quot; @click=\&quot;click1\&quot;&gt;点击{{show1?'隐藏':'展开'}}&lt;/div&gt;\n&lt;xl-collapse-transition&gt;\n  &lt;div v-show=\&quot;show1\&quot;&gt;\n    &lt;div class=\&quot;demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item2\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item1\&quot;&gt;&lt;/div&gt;\n    &lt;div class=\&quot;demo-item2\&quot;&gt;&lt;/div&gt;\n  &lt;/div&gt;\n&lt;/xl-collapse-transition&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        show1: false\n      }\n    },\n    methods: {\n      click1() {\n        this.show1 = !this.show1\n      }\n    }\n  }\n&quot;,&quot;style&quot;:&quot;\n  .demo-item1{\n    height: 4em;\n    background-color: #409eff;\n  }\n  .demo-item2{\n    height: 4em;\n    background-color: #67c23a;\n  }\n  .demo-item3{\n    color: #fff;\n    font-size: 1.5em;\n    text-align: center;\n    line-height: 4em;\n    cursor: pointer;\n    font-weight: bold;\n  }\n  .collapse-transition{\n    transition: all 2s ease-in-out;\n  }\n&quot;}">
                            <div><div class="demo-item2 demo-item3" @click="click1">点击{{show1?'隐藏':'展开'}}</div>
<xl-collapse-transition>
  <div v-show="show1">
    <div class="demo-item1"></div>
    <div class="demo-item2"></div>
    <div class="demo-item1"></div>
    <div class="demo-item2"></div>
  </div>
</xl-collapse-transition>



</div>
                            <div slot="desc"><p>折叠组件,可以指定class <code>collapse-transition</code>来自定义动画效果，通常内容是<code>transition: all 2s ease-in-out;</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item2 demo-item3'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"click1"</span>&gt;</span>点击{{show1?'隐藏':'展开'}}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-collapse-transition</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"show1"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item1'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'demo-item2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-collapse-transition</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.demo-item1</span>{
    <span class="hljs-attribute">height</span>: <span class="hljs-number">4em</span>;
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#409eff</span>;
  }
  <span class="hljs-selector-class">.demo-item2</span>{
    <span class="hljs-attribute">height</span>: <span class="hljs-number">4em</span>;
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#67c23a</span>;
  }
  <span class="hljs-selector-class">.demo-item3</span>{
    <span class="hljs-attribute">color</span>: <span class="hljs-number">#fff</span>;
    <span class="hljs-attribute">font-size</span>: <span class="hljs-number">1.5em</span>;
    <span class="hljs-attribute">text-align</span>: center;
    <span class="hljs-attribute">line-height</span>: <span class="hljs-number">4em</span>;
    <span class="hljs-attribute">cursor</span>: pointer;
    <span class="hljs-attribute">font-weight</span>: bold;
  }
  <span class="hljs-selector-class">.collapse-transition</span>{
    <span class="hljs-attribute">transition</span>: all <span class="hljs-number">2s</span> ease-in-out;
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show1</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      click1() {
        <span class="hljs-keyword">this</span>.show1 = !<span class="hljs-keyword">this</span>.show1
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>
<style>
  .demo-item1{
    height: 4em;
    background-color: #409eff;
  }
  .demo-item2{
    height: 4em;
    background-color: #67c23a;
  }
  .demo-item3{
    color: #fff;
    font-size: 1.5em;
    text-align: center;
    line-height: 4em;
    cursor: pointer;
    font-weight: bold;
  }
  .collapse-transition{
    transition: all 2s ease-in-out;
  }
</style>
<script>
  export default {
    data() {
      return {
        show1: false
      }
    },
    methods: {
      click1() {
        this.show1 = !this.show1
      }
    }
  }
</script>


